.app__newCV {
  display: grid;
  grid-template-columns: 300px 500px 300px;
  grid-template-rows: minmax(295px, max-content) 20px minmax(75px, max-content);
  column-gap: 20px;
  row-gap: 20px;
  margin-bottom: 50px;
}

.ava__track { 
  grid-column: 1/2;
  grid-row: 1/2;
}

.ava__track_img2{
  display: block;
  width: 250px;
  margin: 0 auto;
}

.about_me__track {
  grid-column: 2/4;
  grid-row: 1/2;
  margin-bottom: 40px;
}

.expirience__track {
  grid-column: 1/2;
  grid-row: 2/3;
}

.portfolio__track {
  grid-column: 2/3;
  grid-row: 2/3;
}

.courses__track {
  grid-column: 3/4;
  grid-row: 2/3;
}

.courses_info__track {
  margin-top: 0;
  grid-column: 1/2;
  grid-row: 3/4;
  :first-child {
    margin-top: 45px; 
    padding-left: 0;
  }
}

.portfolio_info__track {
  margin-top: 45px;
  grid-column: 2/3;
  grid-row: 3/4;
  display: grid;
  grid-template-columns: 250px 250px;
  grid-template-rows: minmax(150px, max-content) minmax(150px, max-content);
  column-gap: 20px;
  row-gap: 20px;
  img {
    max-width: 220px;
    height: 90%;
  } 
}

.courses_info__track p {
  margin-top: 15px;
  font-size: 24px;
  cursor: pointer;
  padding-left: 15px;
}

.courses_info__track span.active-style { 
  color: #17bbdb; 
  font-weight: 700; 
  text-decoration: none; 
  border: 0; 
}

.description_info__track {
  margin-top: 45px;
  grid-column: 3/4;
  grid-row: 3/4;
  font-size: 16px;
  font-weight: 500;
  span {
    font-size: 20px;
    font-weight: 700;
  }
  :first-child {
    margin-bottom: 30px;
  }
}

.courses__item_selected {
  box-shadow: 0 5px 20px #17bbdb;
  transform: translateY(-8px);
}

.courses__item_clicked {
  box-shadow: 0 5px 20px #17bbdb;
  transform: translateY(-8px);
}


.nextPage {
  display: block;
  margin: 0 auto;
  width: 250px;
  height: 75px;
  div {
    font-size: 15px;
  }
}